.relationships {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 131px;
  padding-bottom: 135px;
  background-color: #F6F5F6;

  &__container {
    width: 1140px;
    padding: 0 15px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    color: #000000;
    text-align: center;
    width: 540px;
  }

  &__list {
    margin-top: 50px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 30px;

    &-item {

      &-info {

        &-title {
          margin-top: 16px;
        }

        &-description {
          margin-top: 10px;
          color: #4c4b4b;
        }
      }
    }
  }
}

@media (max-width: 1170px) {
  .relationships {

    &__container {
      width: 929px;
    }

    &__title {
    }

    &__list {

      &-item {

        &-info {

          &-title {
          }
        }
      }
    }
  }
}

@media (max-width: 960px) {
  .relationships {
    padding-top: 89px;
    padding-bottom: 83px;

    &__container {
      width: 721px;
    }

    &__list {
      grid-template-columns: 1fr;
      row-gap: 34px;
      margin-top: 42px;

      &-item {
        display: flex;
        padding: 0 57px;

        &-img {
          transform: scale(0.9);
        }

        &-info {
          margin-left: 31px;

          &-title {
            margin-top: 0;
          }

          &-description {
            margin-top: 8px;
          }
        }
      }
    }
  }
}

@media (max-width: 755px) {
  .relationships {
    padding-top: 54px;
    padding-bottom: 34px;

    &__container {
      width: 100vw;
      padding: 0 15px;
      box-sizing: border-box;
    }

    &__title {
      width: 275px;
    }

    &__list {
      margin-top: 27px;
      row-gap: 0;

      &-item {
        flex-direction: column;
        align-items: center;
        padding: 0;
        margin-bottom: 22px;

        &-img {
        }

        &-info {
          margin-left: 0;

          &.info-js-fix {
            margin-top: 7px;
          }

          &-title {
            text-align: center;
          }

          &-description {
            text-align: center;
            margin-top: 3px;
          }
        }
      }
    }
  }
}
